@import "https://fonts.googleapis.com/css?family=Audiowide";
* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  overflow-y: overlay;
  background-color: #1a1a1a;
  width: 100%;
  min-height: 100vh;
  font-family: Audiowide, sans-serif;
}

.header {
  z-index: 100;
  background-color: #000;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 20px 50px;
  transition: all .3s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.header.sticky {
  padding-block: 10px;
  box-shadow: inset 0 -1.5px 3px #ffffff71;
}

.logo {
  cursor: pointer;
  color: #fff;
  font-size: 45px;
  font-weight: 700;
}

.navbar {
  align-items: center;
  display: flex;
}

.nav-links {
  margin-right: 20px;
  list-style: none;
  display: flex;
}

.nav-links li {
  margin-right: 20px;
}

.nav-links a {
  color: #fff;
  padding: 10px;
  font-size: 1.1em;
  text-decoration: none;
  transition: border .3s;
  position: relative;
}

.nav-links a:after {
  content: "";
  background-color: #fff;
  width: 0%;
  height: 2px;
  transition: width .3s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav-links a:hover:after {
  width: 100%;
}

.search-bar {
  justify-content: center;
  align-items: center;
  margin-left: 20px;
  display: flex;
}

.search-bar input {
  color: #fff;
  background-color: #333;
  border: 1px solid #ccc;
  border-radius: 25px;
  outline: none;
  width: 200px;
  padding: 10px 15px;
  font-size: 1em;
  transition: all .3s;
}

.search-bar input:focus {
  border-color: #007bff;
  width: 250px;
  box-shadow: 0 0 10px #007bff80;
}

.search-bar button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 25px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 1em;
  transition: background-color .3s;
  box-shadow: 0 4px 6px #0000001a;
}

.search-bar button:hover {
  background-color: #0056b3;
}

#news-container {
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  width: 90%;
  margin-top: 120px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  display: grid;
}

.news-item {
  text-align: center;
  background-color: #333;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  transition: transform .2s, box-shadow .2s;
  display: flex;
  box-shadow: 0 4px 8px #0000001a;
}

.news-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px #0003;
}

.news-item h2 {
  color: #fff;
  margin-bottom: 10px;
  font-size: 1.5em;
}

.news-item p {
  color: #ccc;
  font-size: 1em;
  line-height: 1.5;
}

.news-item a {
  color: #007bff;
  font-weight: bold;
  text-decoration: none;
}

.news-item a:hover {
  text-decoration: underline;
}

.news-item img.news-image {
  object-fit: cover;
  border-radius: 10px;
  max-width: 100%;
  height: auto;
  margin-bottom: 15px;
}

footer {
  color: #fff;
  text-align: center;
  background-color: #333;
  width: 100%;
  margin-top: 50px;
  padding: 20px 0;
  font-size: .9em;
  position: relative;
  bottom: 0;
}

footer a {
  color: #007bff;
  margin-left: 5px;
  font-weight: bold;
  text-decoration: none;
}

footer a:hover {
  text-decoration: underline;
}

@media (width <= 768px) {
  #news-container {
    grid-template-columns: 1fr;
  }

  .nav-links {
    text-align: center;
    z-index: 99;
    background-color: #333;
    flex-direction: column;
    width: 150px;
    display: none;
    position: absolute;
    top: 60px;
    right: 20px;
  }

  .nav-active {
    display: flex;
  }

  .search-item {
    margin-top: 20px;
  }

  .search-bar {
    flex-direction: column;
    align-items: stretch;
    width: 100%;
  }

  .search-bar input, .search-bar button {
    width: 90%;
    margin: 10px auto;
  }

  .burger {
    display: flex;
  }
}

.burger {
  cursor: pointer;
  flex-direction: column;
  justify-content: space-between;
  height: 21px;
  margin-left: 10px;
  display: none;
}

.burger div {
  background-color: #fff;
  width: 25px;
  height: 3px;
  margin: 3px;
}
